body {
  margin: 3%;
}

.btn1 {
  border: 3px solid beige;
  border-radius: 10px;
  color: whitesmoke;
  background-color: teal;
  padding: 0.3em;
  padding-left: 1em;
  padding-right: 1em;
}
.btn1:hover {
  background-color: #fa7268;
}
.btn1:active {
  background-color: grey;
  color: black;
}

.btn {
  border: none;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background: none;
  cursor: pointer;
  padding: 5px 20px;
  display: inline-block;
  margin: auto;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 600;
  outline: none;
  position: relative;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  transition: all 0.3s;
}

.btn-2 {
  background: #cb4e4e;
  color: #fff;
  box-shadow: 0 6px #ab3c3c;
  -webkit-transition: none;
  -moz-transition: none;
  transition: none;
  margin-bottom: 0.9em;
}

/* Button 2d */
.btn-2d {
  border-radius: 5px;
}

.btn-2d:hover {
  box-shadow: 0 8px #ab3c3c;
  top: -2px;
}

.btn-2d:active {
  box-shadow: 0 0 #ab3c3c;
  top: 6px;
}

/* Button 2g */
.btn-2g {
  border-radius: 40px;
}

.btn-2g:hover {
  box-shadow: 0 4px #ab3c3c;
  top: 2px;
}

.btn-2g:active {
  box-shadow: 0 0 #ab3c3c;
  top: 6px;
}

